:root {
  --main-color: #00acdc;
  --header-height: 60px;
}

body {
  margin: 0;
}

header {
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

ul, li, a {
  all: unset;
}

h1 {
  display: none;
}

.container {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.header_logo {
  width: 40px;
  height: 40px;
}

.header_logo_link {
  height: 40px;
}

.header_list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header_item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
}

.header_link {
  height: var(--header-height);
  line-height: var(--header-height);
  display: block;
}

.header_item:last-child {
  margin-right: 0;
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header_item:hover {
  color: var(--main-color);
  cursor: pointer;
}
